___CSS_LOADER_EXPORT___.push([module.id, `#app {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.controls {
  display: grid;
  place-content: center;
  margin-top: 8px;
}

.boards {
  display: flex;
  justify-content: space-around;
  gap: 10px;
}

.board {
  display: grid;
  height: 450px;
  width: 450px;
  grid-template-rows: repeat(10, 1fr);
  grid-template-columns: repeat(10, 1fr);
  gap: 3px;
  padding: 3px;
  border-radius: 5px;
  border: 0.15em solid black;
}

.cell {
  border-radius: 15px;
  display: grid;
  place-content: center;
  text-align: center;
  font-size: 2rem;
  cursor: crosshair;
  background-color: skyblue;
  transition: background-color 0.5s ease-in-out;
}

.hit {
  background-color: grey;
}

.ship {
  background-color: darkslateblue;
}

.hit-ship {
  background-color: tomato;
}

.sunk-ship {
  background-color: darkred ;
}

.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

.modal-content {
  background-color: #fefefe;
  font-family: sans-serif;
  border-radius: 5px;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 2px solid black;
  box-shadow: 3px 3px;
  width: 30%; /* Could be more or less, depending on screen size */
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .boards {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .board {
    height: 300px;
    width: 300px;
  }

  .modal {
    padding-top: 50%;
  }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,6BAA6B;AAC/B;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,6BAA6B;EAC7B,SAAS;AACX;;AAEA;EACE,aAAa;EACb,aAAa;EACb,YAAY;EACZ,mCAAmC;EACnC,sCAAsC;EACtC,QAAQ;EACR,YAAY;EACZ,kBAAkB;EAClB,0BAA0B;AAC5B;;AAEA;EACE,mBAAmB;EACnB,aAAa;EACb,qBAAqB;EACrB,kBAAkB;EAClB,eAAe;EACf,iBAAiB;EACjB,yBAAyB;EACzB,6CAA6C;AAC/C;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,wBAAwB;AAC1B;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,aAAa,EAAE,sBAAsB;EACrC,eAAe,EAAE,kBAAkB;EACnC,UAAU,EAAE,eAAe;EAC3B,OAAO;EACP,MAAM;EACN,WAAW,EAAE,eAAe;EAC5B,YAAY,EAAE,gBAAgB;EAC9B,cAAc,EAAE,4BAA4B;EAC5C,4BAA4B,EAAE,mBAAmB;EACjD,iCAAiC,EAAE,qBAAqB;AAC1D;;AAEA;EACE,yBAAyB;EACzB,uBAAuB;EACvB,kBAAkB;EAClB,gBAAgB,EAAE,kCAAkC;EACpD,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,UAAU,EAAE,oDAAoD;EAChE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE;IACE,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;EACX;;EAEA;IACE,aAAa;IACb,YAAY;EACd;;EAEA;IACE,gBAAgB;EAClB;AACF","sourcesContent":["#app {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-around;\n}\n\n.controls {\n  display: grid;\n  place-content: center;\n  margin-top: 8px;\n}\n\n.boards {\n  display: flex;\n  justify-content: space-around;\n  gap: 10px;\n}\n\n.board {\n  display: grid;\n  height: 450px;\n  width: 450px;\n  grid-template-rows: repeat(10, 1fr);\n  grid-template-columns: repeat(10, 1fr);\n  gap: 3px;\n  padding: 3px;\n  border-radius: 5px;\n  border: 0.15em solid black;\n}\n\n.cell {\n  border-radius: 15px;\n  display: grid;\n  place-content: center;\n  text-align: center;\n  font-size: 2rem;\n  cursor: crosshair;\n  background-color: skyblue;\n  transition: background-color 0.5s ease-in-out;\n}\n\n.hit {\n  background-color: grey;\n}\n\n.ship {\n  background-color: darkslateblue;\n}\n\n.hit-ship {\n  background-color: tomato;\n}\n\n.sunk-ship {\n  background-color: darkred ;\n}\n\n.modal {\n  display: none; /* Hidden by de